    </div>
    <div
      class="bar-item-box"
      @click="$router.push('/notice/list?type=2')">
      <el-badge
        :value="Number(messageCount)"
        :max="99"
        :hidden="Number(messageCount)<=0">
        <el-tooltip
          class="item"
          effect="dark"
          content="店铺通知"
          placement="bottom">
          <div class="item">
            <i class="iconfont iconxiaoxi" />
