          shape="circle"
          :size="26"
          :src="shop_logo_url" />
        <div class="shop_name">
          {{ shop_name }}
        </div>
        <!-- <i class="el-icon-caret-bottom" /> -->
      </div>
      <el-dropdown-menu
        slot="dropdown"
        class="dropdown-list">
        <el-dropdown-item>
          <span
            style="display:block;"
            @click="handleLogout">退出</span>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
