<script>
import QRCode from 'qrcodejs2'
import { loginOut } from '@/api/login'
import util from '@/utils/util'
import { imConnect } from '@/api/shop'
import { noticeList } from '@/api/notice'
let page
export default {
  data () {
    return {
      share_url_qrcode: undefined,
      imageUrl: '',
      activeIndex: '1',
      msg_num: '',
      messageCount: '',
      activeIndex2: '1'
      // 下面两条 hack 报错，暂时不知道干嘛用的
    }
  },
  computed: {
    shop_name () {
      return this.info.shop_name
    },
    shop_logo_url () {
      return this.info.group_header
    }
  },
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      }
    },
    done: {
      type: Boolean,
      default: () => {
        return false
      }
    }
  },
  watch: {
    done (val) {
      if (val == true) {
        this.autoInitQrCode()
      }
    }
  },
  methods: {
    goToIm () {
      this.msg_num = 0
      window.open('http://im.lehe.com/new/higo_shoper')
    },
    openWiki () {
      window.open('https://higo.atlassian.net/wiki/spaces/HIGO/overview')
    },
    handleLogout () {
      loginOut()
        .then(res => {
          util.removeStore('access_token')
          this.$router.push({
            path: '/login'
          })
        })
        .catch(err => {
          this.$message(err.message)
          console.log(err) // todo 网络请求不成功也直接removeStorage
        })
    },
    async countMessage () {
      try {
        let res = await noticeList({
          page_size: 1,
          notice_type: 2,
          is_read: 0
        })
        this.messageCount = res.data.total
      } catch (err) {
        this.$message.error(err.message)
      }
    },
    autoInitQrCode () {
      let text = this.info.share_url
      this.share_url_qrcode = new QRCode('qrcode', {
        text,
        width: 180,
        height: 180
      })
    }
    // handleSelect(key, keyPath) {
    // console.log(key, keyPath);
    // }
  },
  async mounted () {
    page = this
    await this.countMessage()
    try {
      let res = await imConnect()
      this.msg_num = res.data.msg_num
    } catch (err) {
      this.$message.error(err.message)
    }
  },
  updated () {}
}
</script>
