<template>
  <div class="app">
    <div
      class="menu"
      :style="{width:isCollapse?'64px':'240px'}">
      <div
        class="menu-logo flex"
        :style="{ paddingLeft:isCollapse?'14px':'24px'}"
        @click="goToIndex">
        <span class="logo">HIGO</span>
        <span
          class="title"
          v-show="!isCollapse">买手店工作台</span>
      </div>
      <div class="el-menus">
        <el-menu
          style="transition:none;border-right:none;"
          :collapse="isCollapse"
          :default-active="$route.path"
          :unique-opened="true"
          :default-openeds="openeds"
          text-color="#fff"
          router>
          <!-- <el-submenu
            :index="String(index)"
            v-for="(head,index) in menuConfig"
            :key="head.id">
            <template #title>
              <i
                :class="head.icon"
                class="menu-icon" />
              <span>{{ head.name }}</span>
            </template>
            <el-menu-item
              v-for="(item,index2) in head.children"
              :index="item.value"
              :route="item.value"
              :key="index2">
              {{ item.name }}
            </el-menu-item>
          </el-submenu> -->
          <NavMenu
            :nav-menus="menuConfig"
            :collapse="isCollapse" />
        </el-menu>
      </div>
    </div>
    <div
      class="layout-content"
      :style="{ marginLeft: isCollapse?'64px':'240px'}">
      <header
        class="header flex"
        :style="{ width:isCollapse? 'calc(100% - 64px)':'calc(100% - 240px)'}">
        <div
          style="display:none"
          class="collapse flex-1"
          @click="changeCollapse">
          <i
            class="iconfont"
            :class="isCollapse?'iconindent':'iconoutdent'" />
        </div>
        <TopBar
          class="flex-1"
          :done="done"
          :info="my_shop_info" />
      </header>
      <div
        class="view"
        id="view">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
import menuConfig from './nav.json'
import TopBar from '@/components/TopBar'
import logo from '../../images/login/app.png'
import { MyShopInfo } from '@/api/index'
import { getAccountInfo } from '@/api/login'
import { mapMutations } from 'vuex'
import NavMenu from '@/components/NavMenu'

export default {
  components: {
    TopBar,
    NavMenu
  },
  computed: {
    openeds () {
      let arr = ['0']
      return arr
    }
  },
  data () {
    return {
      isCollapse: false,
      menuConfig,
      logo: Object.freeze(logo),
      my_shop_info: {},
      done: false
    }
  },
  mounted () {
    this.fetch()
  },

  methods: {
    ...mapMutations([
      'SET_SHOP_INFO', // 将 `this.increment()` 映射为 `this.$store.commit('increment')`
      'SET_ACCOUNT_INFO'
    ]),
    fetch () {
      MyShopInfo()
        .then(res => {
          this.my_shop_info = res.data
          this.SET_SHOP_INFO(res.data)
          this.done = true
        })
        .catch(err => {
          this.$message.error(err.message)

          console.log(err)
        })
      getAccountInfo()
        .then(res => {
          console.log(res)
          this.SET_ACCOUNT_INFO(res.data)
        })
        .catch(err => {
          console.log(err)
          this.$message.error(err.message)
        })
    },
    changeCollapse () {
      this.isCollapse = !this.isCollapse
    },
    goToIndex () {
      this.$router.push('/')
    }
  }
}
</script>
