
import request from '@/utils/request'
// 最近使用类目
export function noticeDetail (params = {}) {
  return request({
    url: '/notice/noticeInfo/',
    data: params
  })
}
export function noticeList (params = {}) {
  return request({
    url: '/notice/NoticeList',
    data: params
  })
}
export function readNotice (params = {}) {
  return request({
    url: '/notice/ChangeAllNoticeToRead',
    data: params
  })
}
