<template>
  <div>
    <div class="login-container">
      <div class="browser-tip" v-if="!isChrome">
        为了更好的体验，请您使用Chrome浏览器操作。请点击<a href="https://www.google.com/chrome/" target="_blank">下载</a>
      </div>
      <div class="login-column">
        <div class="login-box">
          <span class="join-us" @click="isJoinHIGOVisible = true">商家入驻</span>
          <p class="logo" />
          <p class="slogan">
            时尚买手管理后台
          </p>
          <div class="code-container">
            <div
              id="qrcode"
              ref="qrcode" />
            <div
              class="code-status wait-scan"
              v-show="state === 0">
              <div class="qrcode-logo" />
            </div>
            <div
              class="code-status invalidated-code"
              v-show="state === 1">
              <p class="login-text">
                二维码已失效
              </p>
              <div
                class="login-button"
                @click="refreshCode">
                请点击刷新
              </div>
            </div>
            <div
              class="code-status scan-success"
              v-show="state === 2">
              <div class="success-icon" />
              <p class="login-text">
                扫码成功
              </p>
            </div>
            <div
              class="code-status confirm-code"
              v-show="state === 3">
              <div class="success-icon" />
              <p class="login-text">
                已确认
              </p>
            </div>
          </div>
          <p class="illustration">
            请使用商家端 APP 扫码登录
          </p>
          <div class="feature">
            <span class="f-item">安全</span>
            <span class="f-divider" />
            <span class="f-item">高效</span>
            <span class="f-divider" />
            <span class="f-item">便捷</span>
          </div>
        </div>
      </div>
    </div>
    <JoinHIGO
      :isJoinHIGOVisible.sync="isJoinHIGOVisible"
    />
  </div>
</template>

<script>
import JoinHIGO from './components/JoinHIGO'
import util from '@/utils/util'
import { qrGetCode, qrGetStatus } from '@/api/login'
import QRCode from 'qrcodejs2'

export default {
  components: {
    JoinHIGO
  },
  data () {
    return {
      showCode: null,
      qrcodeString: null,
      checkLoginTimer: null,
      state: 0,
      isJoinHIGOVisible: false,
      isChrome: false
    }
  },
  created () {
    this.isChrome = !!window.chrome
  },
  methods: {
    getCode () {
      qrGetCode().then((res) => {
        this.qrcodeString = res.data.qrCode

        if (this.showCode !== null) {
          this.showCode.clear()
          this.showCode.makeCode(res.data.qrUrl)
        } else {
          this.showCode = new QRCode(this.$refs.qrcode, {
            text: res.data.qrUrl,
            width: 180,
            height: 180
          })
        }
        this.checkLoginTimer = setInterval(() => {
          this.checkLoginStatus()
        }, 2000)
      }).catch(err => {
        this.$message.error(err.message)

        console.log(err)
      })
    },
    checkLoginStatus () {
      const qrCode = this.qrcodeString
      console.log('checkLoginStatus', qrCode)
      const data = {
        qrCode
      }
      qrGetStatus(data).then((res) => {
        if (typeof res === 'string') {
          res = JSON.parse(res)
        }
        if (res.error_code !== 0) {
          this.showInvalidCode()
        } else {
          let status = res.data.status
          switch (status) {
            case '1':
              break
            case '2':
              this.state = 2
              break
            case '3':
              this.state = 3
              util.setStore('access_token', res.data.access_token)
              setTimeout(() => {
                // if (utils.qsObj._r) {
                //     location.href = utils.currentHost + "/dashboard/?_r=" + encodeURIComponent(utils.qsObj._r);
                // } else {
                clearInterval(this.checkLoginTimer)
                this.$router.push('layout/index')
                // }
              }, 1500)
              break
            case '-1':
              this.showInvalidCode()
              break
          }
        }
      }).catch(err => {
        console.log('checkLoginStatus wrong', err)
        this.showInvalidCode()
        this.$message.error(err.message)
      })
    },
    showInvalidCode () {
      clearInterval(this.checkLoginTimer)
      this.state = 1
    },
    refreshCode () {
      qrGetCode().then((res) => {
        if (typeof res === 'string') {
          res = JSON.parse(res)
        }
        this.qrcodeString = res.data.qrCode
        console.log('res.data.qrCode', res.data.qrCode)
        if (this.showCode !== null) {
          this.showCode.clear()
          this.showCode.makeCode(res.data.qrUrl)
        } else {
          this.showCode = new QRCode(document.getElementById('qrcode'), {
            text: res.data.qrUrl,
            width: 180,
            height: 180
          })
        }
        this.checkLoginTimer = setInterval(() => {
          this.checkLoginStatus()
        }, 2000)
        this.state = 0
      }).catch(err => {
        this.$message.error(err.message)

        this.showInvalidCode()
        console.log(err)
      })
    }
  },
  async mounted () {
    await this.getCode()
  }
}
</script>

