import axios from 'axios'
import { Message } from 'element-ui'
import qs from 'qs'
// import store from '../vuex'
import _utils from '@/utils/util'
// import Cookies from 'js-cookie'

// 请求头携带cookie
// axios.defaults.withCredentials = true

const newAxios = async (options) => {
  return new Promise((resolve, reject) => {
    axios(options)
      .then((res) => {
        switch (res.data.error_code) {
          case 0:
            resolve(res.data)
            break
          case 9003 :
          case 9004 :
          case 9401 :
          case 9410 :
          case 9902 :
          case 20003:
            window.location.href = process.env.BASE_URL + 'login'
            break
          default:
            reject(res.data)
            break
        }
      })
      .catch((err) => {
        console.log(err)
        let message = ''
        switch (err.response.status) {
          case 400:
            message = '请求错误'
            break

          case 401:
            message = '未授权，请登录'
            break

          case 403:
            message = '拒绝访问'
            break

          case 404:
            message = `请求地址不存在: ${err.response.config.url}`
            break

          case 408:
            message = '请求超时'
            break

          case 500:
            message = '服务器内部错误'
            break

          case 501:
            message = '服务未实现'
            break

          case 502:
            message = '网关错误'
            break

          case 503:
            message = '服务不可用'
            break

          case 504:
            message = '网关超时'
            break

          case 505:
            message = 'HTTP版本不受支持'
            break

          default:
        }
        console.log(err)
        Message({
          message: err.response.status + ':' + message,
          type: 'error'
        })
        reject(err)
      })
  })
}

const require = (option) => {
  if (!_utils.getStore('access_token') && document.cookie) {
    let cookies = document.cookie.split(';')
    cookies.map((item) => {
      let pair = item.split('=')
      if (/access_token/.test(pair[0])) {
        _utils.setStore('access_token', pair[1])
      }
    })
  }
  let tempToken = _utils.urlParse(window.location.href).access_token || ''
  let accessToken = tempToken || _utils.getStore('access_token')

  let method = typeof option.method === 'string' ? option.method.toUpperCase() : 'GET'

  option.data = Object.assign({ access_token: accessToken }, option.data || {})

  let options = {
    headers: option.headers || {
      'Accept': 'application/json',
      'Content-Type': (method !== 'GET') ? 'application/x-www-form-urlencoded' : 'application/json'
    },
    url: option.url || '/',
    baseURL: option.baseURL || 'https://m.lehe.com/api/biz',
    timeout: 30000,
    method: method || '',
    data: qs.stringify(option.data || {}),
    params: method === 'GET' ? option.data : {},
    withCredentials: true
  }
  return newAxios(options)
}
export default require
